import { AlertTriangle, CheckCircle, Heart, Scale, Shield, Users } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

import { Alert, AlertDescription } from '@/components/ui/alert';
import { AppHeader } from '@/components/ui/app-header';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import PageContainer from '@/components/ui/page-container';

function QuemSomosPage() {
  const navigate = useNavigate();

  const prohibitedItems = [
    'Armas de fogo, munições e explosivos',
    'Drogas ilícitas e substâncias controladas',
    'Medicamentos e produtos farmacêuticos',
    'Produtos químicos perigosos',
    'Itens roubados ou de origem duvidosa',
    'Documentos pessoais e cartões de crédito',
    'Animais vivos',
    'Produtos falsificados ou pirateados',
    'Itens com direitos autorais sem autorização',
    'Equipamentos de vigilância não autorizados',
  ];

  const userRules = [
    'Ser maior de 18 anos e possuir documentos válidos',
    'Fornecer informações verdadeiras no cadastro',
    'Manter itens em bom estado de conservação',
    'Cumprir prazos acordados de devolução',
    'Tratar outros usuários com respeito',
    'Comunicar problemas imediatamente',
    'Não sublocar itens emprestados',
    'Responsabilizar-se por danos durante o uso',
  ];

  return (
    <div className="min-h-screen bg-background">
      <AppHeader showBackButton onBack={() => navigate(-1)} title="Quem Somos" />

      <PageContainer className="py-8">
        <div className="mx-auto max-w-4xl space-y-8">
          {/* Header */}
          <div className="space-y-4 text-center">
            <h1 className="fluid-hero text-foreground">Quem Somos</h1>
            <p className="fluid-body mx-auto max-w-2xl text-muted-foreground">
              Conheça nossa plataforma, missão e as regras que garantem uma experiência segura para
              todos
            </p>
          </div>

          {/* Sobre a Plataforma */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl">
                <Heart className="h-6 w-6 text-primary" />
                Nossa Missão
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <p className="leading-relaxed text-muted-foreground">
                O <strong>Empresta aê</strong> é uma plataforma inovadora que conecta pessoas que
                precisam de itens temporariamente com aquelas que possuem esses itens disponíveis
                para empréstimo. Acreditamos na economia colaborativa e no poder da comunidade para
                tornar recursos mais acessíveis e sustentáveis.
              </p>
              <p className="leading-relaxed text-muted-foreground">
                Nossa missão é democratizar o acesso a ferramentas, equipamentos e itens diversos,
                promovendo uma cultura de compartilhamento responsável que beneficia tanto quem
                empresta quanto quem toma emprestado.
              </p>
            </CardContent>
          </Card>

          {/* Como Funciona */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl">
                <Users className="h-6 w-6 text-primary" />
                Como Funciona
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-6 md:grid-cols-2">
                <div className="space-y-3">
                  <h3 className="font-semibold text-foreground">Para quem Empresta:</h3>
                  <ul className="space-y-2 text-sm text-muted-foreground">
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Cadastre seus itens com fotos e descrições
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Defina preços e períodos de empréstimo
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Receba solicitações e aprove empréstimos
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Ganhe dinheiro com itens parados
                    </li>
                  </ul>
                </div>
                <div className="space-y-3">
                  <h3 className="font-semibold text-foreground">Para quem Pega Emprestado:</h3>
                  <ul className="space-y-2 text-sm text-muted-foreground">
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Busque itens próximos à sua localização
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Solicite empréstimos com facilidade
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Pague apenas pelo tempo de uso
                    </li>
                    <li className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      Economize sem precisar comprar
                    </li>
                  </ul>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Segurança */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl">
                <Shield className="h-6 w-6 text-primary" />
                Segurança e Verificação
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <p className="text-muted-foreground">
                A segurança é nossa prioridade. Implementamos diversas medidas para proteger nossa
                comunidade:
              </p>
              <div className="grid gap-4 md:grid-cols-2">
                <div className="flex items-start gap-3 rounded-lg bg-muted/30 p-3">
                  <Badge variant="secondary" className="bg-green-100 text-green-700">
                    <CheckCircle className="mr-1 h-3 w-3" />
                    Verificação
                  </Badge>
                  <div>
                    <p className="text-sm font-medium">Documentos Verificados</p>
                    <p className="text-xs text-muted-foreground">
                      CPF, RG e comprovante de residência
                    </p>
                  </div>
                </div>
                <div className="flex items-start gap-3 rounded-lg bg-muted/30 p-3">
                  <Badge variant="secondary" className="bg-blue-100 text-blue-700">
                    <Shield className="mr-1 h-3 w-3" />
                    Proteção
                  </Badge>
                  <div>
                    <p className="text-sm font-medium">Sistema de Avaliações</p>
                    <p className="text-xs text-muted-foreground">
                      Histórico e reputação dos usuários
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Regras de Uso */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl">
                <Scale className="h-6 w-6 text-primary" />
                Regras de Uso
              </CardTitle>
              <CardDescription>
                Para manter um ambiente seguro e confiável, todos os usuários devem seguir estas
                regras
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div>
                <h3 className="mb-3 font-semibold text-foreground">Requisitos dos Usuários:</h3>
                <div className="grid gap-2">
                  {userRules.map((rule, index) => (
                    <div key={index} className="flex items-start gap-2">
                      <CheckCircle className="mt-0.5 h-4 w-4 flex-shrink-0 text-green-500" />
                      <span className="text-sm text-muted-foreground">{rule}</span>
                    </div>
                  ))}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Itens Proibidos */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl text-red-600">
                <AlertTriangle className="h-6 w-6" />
                Itens Proibidos
              </CardTitle>
              <CardDescription>
                Os seguintes itens são estritamente proibidos em nossa plataforma
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Alert className="mb-4 border-red-200 bg-red-50">
                <AlertTriangle className="h-4 w-4 text-red-600" />
                <AlertDescription className="text-red-800">
                  <strong>Importante:</strong> O cadastro de itens proibidos resultará no banimento
                  imediato da conta e possível comunicação às autoridades competentes.
                </AlertDescription>
              </Alert>
              <div className="grid gap-2">
                {prohibitedItems.map((item, index) => (
                  <div
                    key={index}
                    className="flex items-start gap-2 rounded border border-red-100 bg-red-50 p-2"
                  >
                    <AlertTriangle className="mt-0.5 h-4 w-4 flex-shrink-0 text-red-500" />
                    <span className="text-sm text-red-800">{item}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Responsabilidades */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="flex items-center gap-2 text-xl">
                <Scale className="h-6 w-6 text-primary" />
                Responsabilidades e Compromissos
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid gap-6 md:grid-cols-2">
                <div>
                  <h3 className="mb-3 font-semibold text-foreground">Nossa Responsabilidade:</h3>
                  <ul className="space-y-2 text-sm text-muted-foreground">
                    <li>• Manter a plataforma segura e funcional</li>
                    <li>• Verificar identidade dos usuários</li>
                    <li>• Mediar conflitos quando necessário</li>
                    <li>• Proteger dados pessoais (LGPD)</li>
                    <li>• Cumprir legislação de comércio eletrônico</li>
                  </ul>
                </div>
                <div>
                  <h3 className="mb-3 font-semibold text-foreground">Sua Responsabilidade:</h3>
                  <ul className="space-y-2 text-sm text-muted-foreground">
                    <li>• Fornecer informações verdadeiras</li>
                    <li>• Cuidar bem dos itens emprestados</li>
                    <li>• Cumprir prazos e acordos</li>
                    <li>• Comunicar problemas imediatamente</li>
                    <li>• Respeitar outros usuários</li>
                  </ul>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Informações da Empresa */}
          <Card className="border-0 bg-card/80 shadow-lg backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-xl">Informações da Empresa</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid gap-4 text-sm md:grid-cols-2">
                <div>
                  <p>
                    <strong>Razão Social:</strong> Empresta aê Tecnologia Ltda.
                  </p>
                  <p>
                    <strong>CNPJ:</strong> 00.000.000/0001-00
                  </p>
                  <p>
                    <strong>Endereço:</strong> Rua das Startups, 123 - São Paulo/SP
                  </p>
                </div>
                <div>
                  <p>
                    <strong>E-mail:</strong> contato@empresta-ae.com.br
                  </p>
                  <p>
                    <strong>Telefone:</strong> (11) 9999-9999
                  </p>
                  <p>
                    <strong>Horário:</strong> Segunda a Sexta, 9h às 18h
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Footer da Página */}
          <div className="border-t pt-8 text-center text-sm text-muted-foreground">
            <p>
              Ao usar nossa plataforma, você concorda com nossos{' '}
              <button
                onClick={() => navigate('/termos-uso')}
                className="text-primary hover:underline"
              >
                Termos de Uso
              </button>{' '}
              e{' '}
              <button
                onClick={() => navigate('/politica-privacidade')}
                className="text-primary hover:underline"
              >
                Política de Privacidade
              </button>
            </p>
            <p className="mt-2">Última atualização: Janeiro de 2024</p>
          </div>
        </div>
      </PageContainer>
    </div>
  );
}

export default QuemSomosPage;
