import { Badge } from '@/components/ui/badge';
import { Leaf } from 'lucide-react';

export function HeroSection() {
  return (
    <section className="relative overflow-hidden bg-gradient-to-br from-blue-50 via-white to-purple-50 pt-6 md:pt-8">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-30">
        <div className="absolute left-10 top-10 h-20 w-20 rounded-full bg-blue-200/40" />
        <div className="absolute right-16 top-32 h-16 w-16 rounded-full bg-purple-200/40" />
        <div className="absolute bottom-20 left-20 h-12 w-12 rounded-full bg-emerald-200/40" />
        <div className="absolute bottom-32 right-10 h-24 w-24 rounded-full bg-amber-200/40" />
      </div>

      <div className="container relative mx-auto px-4 py-8 sm:py-12">
        <div className="mx-auto max-w-4xl text-center">
          {/* Título Principal */}
          <h1 className="mb-6 text-4xl font-bold tracking-tight text-gray-900 sm:text-5xl lg:text-6xl">
            Tudo o que você precisa,
            <span className="block text-primary">Está bem perto!</span>
          </h1>

          {/* Subtítulo Explicativo */}
          <p className="mx-auto mb-8 max-w-2xl text-lg text-gray-600 sm:text-xl">
            Ferramentas, equipamentos, eletrônicos e muito mais.
            <strong className="text-gray-800">
              {' '}
              Alugue por dias, retire próximo de você, economize sempre.
            </strong>{' '}
            Ou disponibilize seus itens parados e
            <strong className="text-gray-800"> transforme em renda extra.</strong>
          </p>

          {/* Badge após o texto */}
          <div className="flex justify-center">
            <Badge className="border-emerald-200 bg-emerald-100 px-4 py-2 text-emerald-700">
              <Leaf className="mr-2 h-4 w-4" />
              Consumo Consciente e Sustentável
            </Badge>
          </div>
        </div>
      </div>
    </section>
  );
}
