import {
  Award,
  HelpCircle,
  MessageCircle,
  Minus,
  Package,
  Plus,
  Search,
  Settings,
  Shield,
  User,
  Zap,
} from 'lucide-react';
import React, { useState } from 'react';

import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';

interface FAQItem {
  id: string;
  question: string;
  answer: string;
  category:
    | 'primeiros-passos'
    | 'busca'
    | 'processo'
    | 'seguranca'
    | 'pagamento'
    | 'perfil'
    | 'itens'
    | 'suporte'
    | 'tecnico';
  priority: number;
  hasCTA?: boolean;
  ctaText?: string;
  ctaAction?: () => void;
}

interface FAQSectionProps {
  className?: string;
  onContactClick?: () => void;
  onChatClick?: () => void;
}

const FAQSection: React.FC<FAQSectionProps> = ({ className, onContactClick, onChatClick }) => {
  // Permitir apenas um item aberto por vez
  const [openItem, setOpenItem] = useState<string | null>(null);
  const [selectedCategory, setSelectedCategory] = useState<string>('all');

  const faqData: FAQItem[] = [
    // PRIMEIROS PASSOS
    {
      id: 'faq-1',
      question: 'Como faço para me cadastrar na plataforma?',
      answer:
        'Baixe o app, clique em "Registrar", preencha seus dados pessoais e faça a verificação com CPF, RG e comprovante de residência. O processo leva cerca de 24 horas para aprovação.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-2',
      question: 'Quais documentos preciso para verificar minha conta?',
      answer:
        'Você precisa de CPF, RG (ou CNH) e comprovante de residência atualizado (até 3 meses). Todos os documentos devem estar legíveis e sem rasuras.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-3',
      question: 'Como funciona o processo completo de empréstimo?',
      answer:
        'Busque o item desejado, solicite o empréstimo, aguarde aprovação do proprietário, combine local e horário de retirada pelo chat, retire o item, use conforme acordado e devolva no prazo. Ambos avaliam a experiência ao final.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-4',
      question: 'Posso usar a plataforma sem verificar minha identidade?',
      answer:
        'Não. A verificação de identidade é obrigatória para garantir a segurança de todos os usuários. Contas não verificadas têm acesso limitado apenas para navegação.',
      category: 'primeiros-passos',
      priority: 2,
    },

    // BUSCA E LOCALIZAÇÃO
    {
      id: 'faq-5',
      question: 'Como funciona a busca por localização?',
      answer:
        'O app usa sua localização atual para mostrar itens próximos. Você pode ajustar o raio de busca de 1km até 50km e filtrar por bairro ou região específica.',
      category: 'busca',
      priority: 1,
    },
    {
      id: 'faq-6',
      question: 'Posso alterar minha localização para ver itens de outras regiões?',
      answer:
        'Sim! No filtro de busca, você pode digitar qualquer endereço ou CEP para explorar itens em outras cidades. Útil para planejar empréstimos quando viajar.',
      category: 'busca',
      priority: 2,
    },
    {
      id: 'faq-7',
      question: 'Como são calculadas as distâncias dos itens?',
      answer:
        'As distâncias são calculadas em linha reta entre sua localização e o item. O tempo estimado de deslocamento considera trânsito médio da região.',
      category: 'busca',
      priority: 3,
    },

    // PROCESSO DE EMPRÉSTIMO
    {
      id: 'faq-8',
      question: 'Como combino a entrega/retirada do item?',
      answer:
        'Após aprovação, use o chat integrado para combinar local, horário e detalhes. Recomendamos locais públicos e seguros. Alguns proprietários oferecem entrega por taxa adicional.',
      category: 'processo',
      priority: 1,
    },
    {
      id: 'faq-9',
      question: 'Posso estender o prazo de um empréstimo?',
      answer:
        'Sim, se o proprietário concordar. Solicite a extensão pelo chat antes do vencimento. O valor adicional será calculado proporcionalmente e cobrado automaticamente.',
      category: 'processo',
      priority: 1,
    },
    {
      id: 'faq-10',
      question: 'O que acontece se eu atrasar a devolução?',
      answer:
        'Há uma tolerância de 2 horas. Após isso, é cobrada multa de 20% do valor diário. Atrasos frequentes podem resultar em suspensão da conta.',
      category: 'processo',
      priority: 2,
    },
    {
      id: 'faq-11',
      question: 'Quanto tempo leva para aprovar minha solicitação?',
      answer:
        'A maioria das solicitações é respondida em até 2 horas. Se não houver resposta em 24h, você pode cancelar automaticamente sem custos.',
      category: 'processo',
      priority: 2,
    },
    {
      id: 'faq-12',
      question: 'Posso cancelar um empréstimo após a reserva?',
      answer:
        'Sim. Até 24h antes da retirada: cancelamento gratuito. Entre 24h e 2h antes: taxa de 10%. Menos de 2h antes: taxa de 30% do valor total.',
      category: 'processo',
      priority: 3,
    },

    // SEGURANÇA
    {
      id: 'faq-13',
      question: 'É seguro emprestar itens com pessoas desconhecidas?',
      answer:
        'Sim! Todos os usuários são verificados com documentos, temos sistema de avaliações, seguro contra danos e suporte 24/7. Sempre prefira locais públicos para trocas.',
      category: 'seguranca',
      priority: 1,
    },
    {
      id: 'faq-14',
      question: 'O que acontece se o item for danificado durante o empréstimo?',
      answer:
        'Nosso seguro cobre danos acidentais até R$ 5.000. Comunique imediatamente pelo app, tire fotos e abra um chamado. A análise leva até 48h.',
      category: 'seguranca',
      priority: 1,
    },
    {
      id: 'faq-15',
      question: 'Como funciona o sistema de caução/garantia?',
      answer:
        'Para itens acima de R$ 200, é retido um valor de caução no cartão (não cobrado). A caução é liberada automaticamente após devolução sem problemas.',
      category: 'seguranca',
      priority: 2,
    },
    {
      id: 'faq-16',
      question: 'O que fazer se um item não foi devolvido?',
      answer:
        'Comunique imediatamente pelo app. Temos 48h para mediar o conflito. Se não resolvido, acionamos a caução e, se necessário, medidas legais.',
      category: 'seguranca',
      priority: 2,
    },

    // PAGAMENTO
    {
      id: 'faq-17',
      question: 'Quais são as taxas e como funciona o pagamento?',
      answer:
        'Você paga o valor do aluguel + taxa de serviço de 8%. Aceitamos cartão, PIX e carteira digital. O pagamento é processado apenas após confirmação da retirada.',
      category: 'pagamento',
      priority: 1,
    },
    {
      id: 'faq-18',
      question: 'Como recebo o pagamento pelos meus empréstimos?',
      answer:
        'O pagamento é liberado 24h após a devolução confirmada. Você recebe 92% do valor (descontamos 8% de taxa). Transferência via PIX ou conta bancária.',
      category: 'pagamento',
      priority: 1,
    },
    {
      id: 'faq-19',
      question: 'Posso negociar preços diretamente com o usuário?',
      answer:
        'Não. Todos os pagamentos devem ser feitos pela plataforma para garantir segurança e cobertura do seguro. Negociações externas violam nossos termos.',
      category: 'pagamento',
      priority: 2,
    },
    {
      id: 'faq-20',
      question: 'Como funciona o reembolso em caso de problemas?',
      answer:
        'Se o item não estiver conforme anunciado ou houver problemas graves, você pode solicitar reembolso total em até 2h após a retirada. Análise em até 24h.',
      category: 'pagamento',
      priority: 3,
    },

    // PERFIL E AVALIAÇÕES
    {
      id: 'faq-21',
      question: 'Como funciona o sistema de avaliações?',
      answer:
        'Após cada empréstimo, ambos avaliam a experiência (1-5 estrelas) e podem deixar comentários. Avaliações são públicas e influenciam na reputação do usuário.',
      category: 'perfil',
      priority: 1,
    },
    {
      id: 'faq-22',
      question: 'Como posso melhorar minha avaliação na plataforma?',
      answer:
        'Seja pontual, cuide bem dos itens, comunique-se claramente, cumpra acordos e trate outros usuários com respeito. Usuários bem avaliados têm prioridade nas solicitações.',
      category: 'perfil',
      priority: 2,
    },
    {
      id: 'faq-23',
      question: 'Como edito as informações do meu perfil?',
      answer:
        'Vá em "Meu Perfil" > "Editar Perfil". Você pode alterar foto, descrição e preferências. Dados pessoais (nome, CPF) só podem ser alterados via suporte.',
      category: 'perfil',
      priority: 2,
    },
    {
      id: 'faq-24',
      question: 'Posso bloquear usuários específicos?',
      answer:
        'Sim. No perfil do usuário, clique nos três pontos e selecione "Bloquear". Usuários bloqueados não podem ver seus itens nem solicitar empréstimos.',
      category: 'perfil',
      priority: 3,
    },

    // ITENS E CATEGORIAS
    {
      id: 'faq-25',
      question: 'Como cadastro um item para empréstimo?',
      answer:
        'Clique em "+" no menu principal, tire fotos nítidas, preencha título, descrição, categoria, preço e condições. Itens são aprovados em até 24h após análise.',
      category: 'itens',
      priority: 1,
    },
    {
      id: 'faq-26',
      question: 'Que tipos de itens posso encontrar na plataforma?',
      answer:
        'Ferramentas, eletrônicos, equipamentos esportivos, instrumentos musicais, itens de festa, roupas especiais, livros, jogos e muito mais. Veja nossa lista de itens proibidos.',
      category: 'itens',
      priority: 1,
    },
    {
      id: 'faq-27',
      question: 'Posso definir regras específicas para meus itens?',
      answer:
        'Sim! Você pode definir horários de retirada, locais preferenciais, instruções de uso, requisitos mínimos de avaliação do locatário e outras condições especiais.',
      category: 'itens',
      priority: 2,
    },
    {
      id: 'faq-28',
      question: 'Como posso confiar na qualidade dos itens?',
      answer:
        'Todos os itens têm fotos, descrições detalhadas, avaliações de outros usuários e histórico de empréstimos. Proprietários com boa reputação são destacados.',
      category: 'itens',
      priority: 2,
    },
    {
      id: 'faq-29',
      question: 'Existe limite de itens que posso cadastrar?',
      answer:
        'Usuários novos podem cadastrar até 5 itens. Com boa reputação, o limite aumenta para 20 itens. Usuários premium têm limite ilimitado.',
      category: 'itens',
      priority: 3,
    },

    // SUPORTE E PROBLEMAS
    {
      id: 'faq-30',
      question: 'Como posso entrar em contato com o suporte?',
      answer:
        'Suporte 24/7 disponível pelo chat do app, WhatsApp (11) 9999-9999, e-mail contato@empresta-ae.com.br ou telefone. Tempo médio de resposta: 15 minutos.',
      category: 'suporte',
      priority: 1,
    },
    {
      id: 'faq-31',
      question: 'Como reporto um problema com um usuário?',
      answer:
        'No perfil do usuário ou chat, clique em "Reportar". Descreva o problema detalhadamente. Nossa equipe analisa em até 24h e toma as medidas necessárias.',
      category: 'suporte',
      priority: 1,
    },
    {
      id: 'faq-32',
      question: 'Como contesto uma avaliação injusta?',
      answer:
        'Vá em "Meu Perfil" > "Avaliações" > "Contestar". Explique sua versão dos fatos com evidências. Nossa equipe analisa e pode remover avaliações inadequadas.',
      category: 'suporte',
      priority: 2,
    },
    {
      id: 'faq-33',
      question: 'Posso cancelar minha conta? Como?',
      answer:
        'Sim. Vá em "Configurações" > "Conta" > "Excluir Conta". Certifique-se de não ter empréstimos pendentes. O processo é irreversível após confirmação.',
      category: 'suporte',
      priority: 3,
    },

    // TÉCNICO
    {
      id: 'faq-34',
      question: 'O app funciona offline?',
      answer:
        'Parcialmente. Você pode ver itens salvos e conversas recentes offline, mas precisa de internet para buscar novos itens, fazer solicitações e enviar mensagens.',
      category: 'tecnico',
      priority: 2,
    },
    {
      id: 'faq-35',
      question: 'Como ativo as notificações?',
      answer:
        'Vá em "Configurações" > "Notificações" e ative as que desejar: novas mensagens, solicitações, lembretes de devolução, ofertas especiais, etc.',
      category: 'tecnico',
      priority: 2,
    },
    {
      id: 'faq-36',
      question: 'Posso usar a plataforma pelo navegador?',
      answer:
        'Atualmente apenas pelo app móvel (iOS e Android). Estamos desenvolvendo a versão web que estará disponível em breve.',
      category: 'tecnico',
      priority: 3,
    },
  ];

  // Filtro de categoria
  const categories = [
    { key: 'all', label: 'Todas', icon: <HelpCircle className="h-4 w-4" /> },
    { key: 'primeiros-passos', label: 'Primeiros Passos', icon: <Zap className="h-4 w-4" /> },
    { key: 'busca', label: 'Busca', icon: <Search className="h-4 w-4" /> },
    { key: 'processo', label: 'Empréstimos', icon: <Package className="h-4 w-4" /> },
    { key: 'seguranca', label: 'Segurança', icon: <Shield className="h-4 w-4" /> },
    { key: 'pagamento', label: 'Pagamento', icon: <Award className="h-4 w-4" /> },
    { key: 'perfil', label: 'Perfil', icon: <User className="h-4 w-4" /> },
    { key: 'itens', label: 'Itens', icon: <Package className="h-4 w-4" /> },
    { key: 'suporte', label: 'Suporte', icon: <MessageCircle className="h-4 w-4" /> },
    { key: 'tecnico', label: 'Técnico', icon: <Settings className="h-4 w-4" /> },
  ];

  // Filtrar FAQ por categoria, depois ordenar por prioridade
  const filteredFAQ = faqData
    .filter((item) => {
      const matchesCategory = selectedCategory === 'all' || item.category === selectedCategory;
      return matchesCategory;
    })
    .sort((a, b) => a.priority - b.priority);

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'primeiros-passos':
        return <Zap className="h-5 w-5 text-primary" />;
      case 'busca':
        return <Search className="h-5 w-5 text-primary" />;
      case 'processo':
        return <Package className="h-5 w-5 text-primary" />;
      case 'seguranca':
        return <Shield className="h-5 w-5 text-primary" />;
      case 'pagamento':
        return <Award className="h-5 w-5 text-primary" />;
      case 'perfil':
        return <User className="h-5 w-5 text-primary" />;
      case 'itens':
        return <Package className="h-5 w-5 text-primary" />;
      case 'suporte':
        return <MessageCircle className="h-5 w-5 text-primary" />;
      case 'tecnico':
        return <Settings className="h-5 w-5 text-primary" />;
      default:
        return <HelpCircle className="h-5 w-5 text-primary" />;
    }
  };

  return (
    <div className={cn('space-y-10', className)}>
      {/* Header do FAQ */}
      <div className="text-center">
        <p className="fluid-body text-muted-foreground">
          Encontre respostas para suas dúvidas sobre cadastro, empréstimos, pagamentos e muito mais.
        </p>
      </div>

      {/* Filtros de categoria modernos */}
      <div className="flex flex-wrap justify-center gap-3">
        {categories.map((cat) => (
          <Button
            key={cat.key}
            variant={selectedCategory === cat.key ? 'default' : 'outline'}
            size="sm"
            onClick={() => setSelectedCategory(cat.key)}
            className={cn(
              'flex items-center gap-2 rounded-full px-4 py-2 transition-all duration-300 hover:scale-105',
              selectedCategory === cat.key
                ? 'bg-primary text-primary-foreground shadow-lg shadow-primary/25'
                : 'border-muted-foreground/20 bg-background/50 hover:bg-muted/50'
            )}
          >
            {cat.icon}
            {cat.label}
          </Button>
        ))}
      </div>

      {/* Lista de FAQs modernizada */}
      <div className="mx-auto max-w-4xl space-y-4">
        {filteredFAQ.length === 0 ? (
          <div className="rounded-3xl border border-muted/50 bg-muted/20 p-12 text-center">
            <div className="mx-auto max-w-sm space-y-4">
              <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-muted">
                <HelpCircle className="h-8 w-8 text-muted-foreground" />
              </div>
              <h3 className="fluid-subtitle font-semibold">Nenhuma pergunta encontrada</h3>
              <p className="text-muted-foreground">
                Tente ajustar sua busca ou selecionar uma categoria diferente.
              </p>
            </div>
          </div>
        ) : (
          filteredFAQ.map((item, index) => (
            <div
              key={item.id}
              className={cn(
                'group overflow-hidden rounded-3xl border-2 transition-all duration-500 hover:shadow-lg',
                openItem === item.id
                  ? 'border-primary/40 bg-gradient-to-br from-primary/5 to-primary/10 shadow-xl shadow-primary/10'
                  : 'border-primary/20 bg-background/80 backdrop-blur-sm hover:border-primary/30 hover:bg-background hover:shadow-primary/5'
              )}
            >
              <button
                className="w-full p-6 text-left transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-primary/20 focus:ring-offset-2"
                onClick={() => setOpenItem(openItem === item.id ? null : item.id)}
                aria-expanded={openItem === item.id}
                aria-controls={`faq-content-${item.id}`}
              >
                <div className="flex items-center justify-between gap-6">
                  <div className="flex flex-1 items-center gap-4">
                    <div className="flex-shrink-0">
                      <div
                        className={cn(
                          'flex h-12 w-12 items-center justify-center rounded-2xl transition-all duration-300',
                          openItem === item.id
                            ? 'bg-primary/20 text-primary shadow-lg shadow-primary/20'
                            : 'bg-muted/50 text-muted-foreground group-hover:bg-primary/10 group-hover:text-primary'
                        )}
                      >
                        {getCategoryIcon(item.category)}
                      </div>
                    </div>
                    <h3 className="fluid-subtitle font-semibold text-foreground transition-colors duration-300 group-hover:text-primary">
                      {item.question}
                    </h3>
                  </div>
                  <div className="flex-shrink-0">
                    <div
                      className={cn(
                        'flex h-10 w-10 items-center justify-center rounded-full transition-all duration-500',
                        openItem === item.id
                          ? 'rotate-180 bg-primary/20 text-primary'
                          : 'bg-muted/50 text-muted-foreground group-hover:bg-primary/10 group-hover:text-primary'
                      )}
                    >
                      {openItem === item.id ? (
                        <Minus className="h-5 w-5" />
                      ) : (
                        <Plus className="h-5 w-5" />
                      )}
                    </div>
                  </div>
                </div>
              </button>

              <div
                className={cn(
                  'overflow-hidden transition-all duration-500 ease-in-out',
                  openItem === item.id ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
                )}
              >
                <div className="border-t border-primary/10 px-6 pb-6 pt-4">
                  <div className="ml-16 animate-fade-in">
                    <p className="fluid-body leading-relaxed text-muted-foreground">
                      {item.answer}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default FAQSection;
