import { Calculator, Calendar, CheckCircle2, Clock, Home, Shield, User } from 'lucide-react';
import { useLocation, useNavigate } from 'react-router-dom';

import { AppHeader } from '@/components/ui/app-header';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import PageContainer from '@/components/ui/page-container';
import { useAuth } from '@/hooks/useAuth';
import { useScrollToTop } from '@/hooks/useScrollToTop';

import type { ItemWithDetails } from '@/types';

interface InsuranceOption {
  id: string;
  name: string;
  description: string;
  coverage: string[];
  price: number;
  deductible: number;
  popular?: boolean;
  recommended?: boolean;
  savings?: string;
}

interface LoanSuccessData {
  item: ItemWithDetails;
  startDate: string;
  endDate: string;
  totalDays: number;
  totalPrice: number;
  insurance?: InsuranceOption | null;
  loanId: string;
}

export function LoanSuccessPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const { scrollToTop } = useScrollToTop();
  const { user } = useAuth();

  const data = location.state as LoanSuccessData;

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL',
    }).format(value);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('pt-BR', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };

  const formatDateShort = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('pt-BR');
  };

  if (!data) {
    navigate('/');
    return null;
  }

  const rentalAmount = data.item.price * data.totalDays;
  const insuranceAmount = data.insurance ? data.insurance.price * data.totalDays : 0;
  const totalAmount = rentalAmount + insuranceAmount;

  return (
    <div className="flex min-h-screen flex-col bg-gradient-to-br from-green-50 via-white to-blue-50">
      <AppHeader
        showBackButton
        onBack={() => navigate('/')}
        title="Locação Confirmada"
        user={
          user
            ? {
                id: 'temp-id',
                name: user.name,
                email: user.email,
                avatar: user.avatar,
                createdAt: new Date().toISOString(),
                updatedAt: new Date().toISOString(),
              }
            : undefined
        }
      />

      <PageContainer className="flex flex-1 flex-col">
        <div className="mx-auto w-full max-w-2xl space-y-6">
          {/* Header de Sucesso */}
          <div className="space-y-4 text-center">
            <div className="flex justify-center">
              <div className="flex h-20 w-20 items-center justify-center rounded-full bg-green-100 shadow-lg">
                <CheckCircle2 className="h-10 w-10 text-green-600" />
              </div>
            </div>
            <div>
              <h1 className="mb-2 text-3xl font-bold text-green-800">Locação Confirmada!</h1>
              <p className="text-lg text-muted-foreground">
                Sua solicitação foi enviada e está sendo processada
              </p>
              <Badge className="mt-2 bg-green-100 text-green-700">Solicitação: {data.loanId}</Badge>
            </div>
          </div>

          {/* Detalhes do Item */}
          <Card className="border-green-200 bg-white shadow-md">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center gap-2">
                <User className="h-5 w-5 text-gray-700" />
                Item Locado
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center gap-4">
                <img
                  src={data.item.images[0] || '/placeholder.svg'}
                  alt={data.item.title}
                  className="h-16 w-16 rounded-lg object-cover shadow-sm"
                />
                <div className="flex-1">
                  <h3 className="text-lg font-semibold text-gray-900">{data.item.title}</h3>
                  <p className="line-clamp-2 text-sm text-gray-600">{data.item.description}</p>
                  <div className="mt-2 flex items-center gap-2">
                    <Badge variant="outline" className="text-xs">
                      {data.item.category?.name}
                    </Badge>
                    <Badge variant="outline" className="text-xs">
                      {formatCurrency(data.item.price)}/dia
                    </Badge>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Período da Locação */}
          <Card className="border-blue-200 bg-white shadow-md">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center gap-2">
                <Calendar className="h-5 w-5 text-gray-700" />
                Período da Locação
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                <div className="space-y-1">
                  <p className="text-sm font-medium text-gray-600">Data de Retirada</p>
                  <p className="text-lg font-semibold">{formatDateShort(data.startDate)}</p>
                  <p className="text-xs text-gray-500">{formatDate(data.startDate)}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm font-medium text-gray-600">Data de Devolução</p>
                  <p className="text-lg font-semibold">{formatDateShort(data.endDate)}</p>
                  <p className="text-xs text-gray-500">{formatDate(data.endDate)}</p>
                </div>
              </div>
              <div className="flex items-center gap-2 rounded-lg bg-blue-50 p-3">
                <Clock className="h-4 w-4 text-blue-600" />
                <span className="text-sm font-medium text-blue-900">
                  Duração: {data.totalDays} {data.totalDays === 1 ? 'dia' : 'dias'}
                </span>
              </div>
            </CardContent>
          </Card>

          {/* Resumo Financeiro */}
          <Card className="border-2 border-primary/20 bg-primary/5">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center gap-2 text-lg">
                <Calculator className="h-5 w-5 text-primary" />
                Resumo Financeiro
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Detalhamento dos valores */}
              <div className="space-y-3">
                {/* Valor do aluguel */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-blue-500" />
                    <span className="text-sm font-medium">
                      Aluguel ({data.totalDays} {data.totalDays === 1 ? 'dia' : 'dias'})
                    </span>
                  </div>
                  <div className="text-right">
                    <p className="font-semibold">{formatCurrency(rentalAmount)}</p>
                    <p className="text-xs text-muted-foreground">
                      {formatCurrency(data.item.price)}/dia
                    </p>
                  </div>
                </div>

                {/* Valor do seguro */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-purple-500" />
                    <span className="text-sm font-medium">
                      {data.insurance ? `Seguro ${data.insurance.name}` : 'Seguro'}
                    </span>
                    {!data.insurance && (
                      <Badge variant="outline" className="text-xs">
                        Não contratado
                      </Badge>
                    )}
                  </div>
                  <div className="text-right">
                    <p className="font-semibold">{formatCurrency(insuranceAmount)}</p>
                    {data.insurance && (
                      <p className="text-xs text-muted-foreground">
                        {formatCurrency(data.insurance.price)}/dia
                      </p>
                    )}
                  </div>
                </div>

                {/* Linha divisória */}
                <div className="border-t border-gray-200" />

                {/* Total */}
                <div className="flex items-center justify-between">
                  <span className="text-lg font-bold">Total</span>
                  <div className="text-right">
                    <p className="text-2xl font-bold text-primary">{formatCurrency(totalAmount)}</p>
                    <p className="text-xs text-muted-foreground">
                      {formatCurrency(totalAmount / data.totalDays)}/dia em média
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Status do Seguro */}
          <Card
            className={`border-2 ${data.insurance ? 'border-green-200 bg-green-50' : 'border-amber-200 bg-amber-50'}`}
          >
            <CardContent className="p-4">
              <div className="flex items-start gap-3">
                <Shield
                  className={`mt-0.5 h-6 w-6 flex-shrink-0 ${data.insurance ? 'text-green-600' : 'text-amber-600'}`}
                />
                <div className="space-y-2">
                  <h4
                    className={`font-semibold ${data.insurance ? 'text-green-900' : 'text-amber-900'}`}
                  >
                    {data.insurance ? 'Proteção Contratada' : 'Sem Proteção'}
                  </h4>
                  <p className={`text-sm ${data.insurance ? 'text-green-800' : 'text-amber-800'}`}>
                    {data.insurance
                      ? `Você contratou o seguro ${data.insurance.name} para esta locação.`
                      : 'Você optou por não contratar seguro para esta locação.'}
                  </p>
                  {data.insurance && (
                    <div className="space-y-2">
                      <div className="flex items-center gap-2">
                        <Badge className="bg-green-100 text-xs text-green-700">
                          Franquia: {formatCurrency(data.insurance.deductible)}
                        </Badge>
                        <Badge className="bg-green-100 text-xs text-green-700">
                          {formatCurrency(data.insurance.price)}/dia
                        </Badge>
                      </div>
                      {data.insurance.coverage.length > 0 && (
                        <div>
                          <p className="mb-1 text-xs font-medium text-green-700">
                            Coberturas incluídas:
                          </p>
                          <div className="flex flex-wrap gap-1">
                            {data.insurance.coverage.map((coverage, index) => (
                              <Badge
                                key={index}
                                variant="secondary"
                                className="bg-green-100 text-xs text-green-700"
                              >
                                {coverage}
                              </Badge>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Próximos Passos */}
          <Card className="border-purple-200 bg-white shadow-md">
            <CardHeader className="pb-3">
              <CardTitle>Próximos Passos</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="flex items-start gap-3">
                <div className="mt-1 h-2 w-2 rounded-full bg-purple-600" />
                <div>
                  <p className="font-medium">Notificação enviada</p>
                  <p className="text-sm text-gray-600">
                    O proprietário foi notificado sobre sua solicitação
                  </p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <div className="mt-1 h-2 w-2 rounded-full bg-purple-600" />
                <div>
                  <p className="font-medium">Aguarde resposta</p>
                  <p className="text-sm text-gray-600">
                    Você receberá uma notificação quando o proprietário responder
                  </p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <div className="mt-1 h-2 w-2 rounded-full bg-purple-600" />
                <div>
                  <p className="font-medium">Pagamento automático</p>
                  <p className="text-sm text-gray-600">
                    O pagamento será processado automaticamente após aprovação
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Botão de Ação */}
          <div className="flex justify-center">
            <Button
              size="lg"
              className="flex items-center gap-2 px-8"
              onClick={() => navigate('/')}
            >
              <Home className="h-4 w-4" />
              Voltar ao Início
            </Button>
          </div>
        </div>
      </PageContainer>
    </div>
  );
}
