import { ChevronLeft, ChevronRight } from 'lucide-react';
import React from 'react';

import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';

interface ImageCarouselProps {
  images: string[];
  currentIndex: number;
  setCurrentIndex: (index: number) => void;
  title: string;
  available: boolean;
}

const ImageCarousel: React.FC<ImageCarouselProps> = ({
  images = [],
  currentIndex = 0,
  setCurrentIndex,
  title,
  available,
}) => {
  if (!images || images.length === 0) {
    return (
      <div className="relative flex aspect-[4/3] items-center justify-center overflow-hidden rounded-lg bg-muted">
        <p className="text-muted-foreground">Nenhuma imagem disponível</p>
      </div>
    );
  }

  const safeIndex = Math.min(Math.max(0, currentIndex), images.length - 1);

  const nextImage = () => {
    setCurrentIndex((safeIndex + 1) % images.length);
  };

  const prevImage = () => {
    setCurrentIndex((safeIndex - 1 + images.length) % images.length);
  };

  // Funções para gestos de swipe
  const [touchStart, setTouchStart] = React.useState<number | null>(null);
  const [touchEnd, setTouchEnd] = React.useState<number | null>(null);
  const minSwipeDistance = 50;

  const onTouchStart = (e: React.TouchEvent) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };

  const onTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;

    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe && images.length > 1) {
      nextImage();
    }
    if (isRightSwipe && images.length > 1) {
      prevImage();
    }
  };

  return (
    <div
      className="relative aspect-[4/3] overflow-hidden rounded-lg bg-muted"
      onTouchStart={onTouchStart}
      onTouchMove={onTouchMove}
      onTouchEnd={onTouchEnd}
    >
      {/* Imagem atual */}
      <img
        alt={`${title} - Imagem ${safeIndex + 1}`}
        className="h-full w-full object-cover"
        src={images[safeIndex]}
      />

      {/* Overlay de navegação */}
      <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent opacity-100 transition-opacity md:opacity-0 md:hover:opacity-100">
        {/* Botões de navegação */}
        {images.length > 1 && (
          <>
            <div className="absolute inset-y-0 left-0 flex items-center">
              <Button
                aria-label="Imagem anterior"
                className="h-full rounded-none bg-black/50 text-white hover:bg-black/70 md:bg-black/30 md:hover:bg-black/50"
                size="icon"
                variant="ghost"
                onClick={prevImage}
              >
                <ChevronLeft className="h-6 w-6 md:h-8 md:w-8" />
              </Button>
            </div>
            <div className="absolute inset-y-0 right-0 flex items-center">
              <Button
                aria-label="Próxima imagem"
                className="h-full rounded-none bg-black/50 text-white hover:bg-black/70 md:bg-black/30 md:hover:bg-black/50"
                size="icon"
                variant="ghost"
                onClick={nextImage}
              >
                <ChevronRight className="h-6 w-6 md:h-8 md:w-8" />
              </Button>
            </div>
          </>
        )}

        {/* Indicadores */}
        {images.length > 1 && (
          <div className="absolute bottom-4 left-1/2 flex -translate-x-1/2 items-center gap-2">
            {images.map((_, index) => (
              <button
                key={index}
                aria-label={`Ir para imagem ${index + 1}`}
                className={cn(
                  'h-3 w-3 rounded-full transition-all md:h-2 md:w-2',
                  index === safeIndex ? 'scale-125 bg-white' : 'bg-white/60 hover:bg-white/80'
                )}
                onClick={() => setCurrentIndex(index)}
              />
            ))}
          </div>
        )}
      </div>

      {/* Badge de disponibilidade */}
      {!available && (
        <div className="absolute right-4 top-4">
          <Badge className="bg-red-600/90 text-white" variant="destructive">
            Indisponível
          </Badge>
        </div>
      )}
    </div>
  );
};

export default ImageCarousel;
