/**
 * Utilitários para trabalhar com CEP
 */

/**
 * Formata um CEP removendo caracteres não numéricos e adicionando hífen
 */
export const formatCep = (cep: string): string => {
  const cleanCep = cep.replace(/\D/g, '');
  if (cleanCep.length <= 5) {
    return cleanCep;
  }
  return cleanCep.replace(/(\d{5})(\d{1,3})/, '$1-$2');
};

/**
 * Valida se um CEP está no formato correto
 */
export const isValidCep = (cep: string): boolean => {
  const cleanCep = cep.replace(/\D/g, '');
  return cleanCep.length === 8;
};

/**
 * Remove formatação do CEP, deixando apenas números
 */
export const cleanCep = (cep: string): string => {
  return cep.replace(/\D/g, '');
};

/**
 * Busca informações de endereço baseado no CEP usando ViaCEP
 */
export const fetchAddressByCep = async (
  cep: string
): Promise<{
  cep: string;
  logradouro: string;
  complemento: string;
  bairro: string;
  localidade: string;
  uf: string;
  erro?: boolean;
} | null> => {
  const cleanedCep = cleanCep(cep);

  if (!isValidCep(cleanedCep)) {
    return null;
  }

  try {
    const response = await fetch(`https://viacep.com.br/ws/${cleanedCep}/json/`);
    const data = await response.json();

    if (data.erro) {
      return null;
    }

    return data;
  } catch (error) {
    console.error('Erro ao buscar CEP:', error);
    return null;
  }
};
