import { Calculator, Shield, ShieldCheck } from 'lucide-react';

import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { cn } from '@/lib/utils';

interface InsuranceOption {
  id: string;
  name: string;
  description: string;
  coverage: string[];
  price: number;
  deductible: number;
  popular?: boolean;
  recommended?: boolean;
  savings?: string;
}

interface LoanFinancialSummaryProps {
  dailyRate: number;
  totalDays: number;
  insurance?: InsuranceOption | null;
  className?: string;
}

export function LoanFinancialSummary({
  dailyRate,
  totalDays,
  insurance,
  className,
}: LoanFinancialSummaryProps) {
  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL',
    }).format(value);
  };

  const rentalAmount = dailyRate * totalDays;
  const insuranceAmount = insurance ? insurance.price * totalDays : 0;
  const totalAmount = rentalAmount + insuranceAmount;

  return (
    <Card className={cn('border-2 border-primary/20 bg-primary/5', className)}>
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center gap-2 text-lg">
          <Calculator className="h-5 w-5 text-primary" />
          Resumo Financeiro
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Detalhamento dos valores */}
        <div className="space-y-3">
          {/* Valor do aluguel */}
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <div className="h-2 w-2 rounded-full bg-blue-500" />
              <span className="text-sm font-medium">
                Aluguel ({totalDays} {totalDays === 1 ? 'dia' : 'dias'})
              </span>
            </div>
            <div className="text-right">
              <p className="font-semibold">{formatCurrency(rentalAmount)}</p>
              <p className="text-xs text-muted-foreground">{formatCurrency(dailyRate)}/dia</p>
            </div>
          </div>

          {/* Valor do seguro */}
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <div className="h-2 w-2 rounded-full bg-purple-500" />
              <span className="text-sm font-medium">
                {insurance ? `Seguro ${insurance.name}` : 'Seguro'}
              </span>
              {!insurance && (
                <Badge variant="outline" className="text-xs">
                  Não contratado
                </Badge>
              )}
            </div>
            <div className="text-right">
              <p className="font-semibold">{formatCurrency(insuranceAmount)}</p>
              {insurance && (
                <p className="text-xs text-muted-foreground">
                  {formatCurrency(insurance.price)}/dia
                </p>
              )}
            </div>
          </div>

          {/* Linha divisória */}
          <div className="border-t border-gray-200" />

          {/* Total */}
          <div className="flex items-center justify-between">
            <span className="text-lg font-bold">Total</span>
            <div className="text-right">
              <p className="text-2xl font-bold text-primary">{formatCurrency(totalAmount)}</p>
              <p className="text-xs text-muted-foreground">
                {formatCurrency(totalAmount / totalDays)}/dia em média
              </p>
            </div>
          </div>
        </div>

        {/* Status do seguro */}
        <div
          className={cn(
            'rounded-xl border-2 p-5',
            insurance ? 'border-green-200 bg-green-50' : 'border-amber-200 bg-amber-50'
          )}
        >
          <div className="flex items-start gap-4">
            {insurance ? (
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-green-100">
                <ShieldCheck className="h-6 w-6 text-green-600" />
              </div>
            ) : (
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-amber-100">
                <Shield className="h-6 w-6 text-amber-600" />
              </div>
            )}
            <div className="min-w-0 flex-1 space-y-3">
              <div>
                <h4
                  className={cn(
                    'text-lg font-semibold',
                    insurance ? 'text-green-900' : 'text-amber-900'
                  )}
                >
                  {insurance ? 'Proteção Ativa' : 'Sem Proteção'}
                </h4>
                <p
                  className={cn(
                    'text-sm leading-relaxed',
                    insurance ? 'text-green-800' : 'text-amber-800'
                  )}
                >
                  {insurance
                    ? `Você está protegido com o seguro ${insurance.name}`
                    : 'Você optou por não contratar seguro para esta locação.'}
                </p>
              </div>

              {insurance && (
                <div className="space-y-3">
                  {/* Informações do seguro */}
                  <div className="flex flex-wrap items-start gap-2">
                    <Badge className="bg-green-100 px-3 py-1 text-green-800">
                      {formatCurrency(insurance.price)}/dia
                    </Badge>
                    <Badge className="bg-green-100 px-3 py-1 text-green-800">
                      Franquia: {formatCurrency(insurance.deductible)}
                    </Badge>
                  </div>

                  {/* Coberturas */}
                  {insurance.coverage.length > 0 && (
                    <div className="w-full">
                      <p className="mb-2 text-sm font-medium text-green-700">
                        Coberturas incluídas:
                      </p>
                      <div className="flex flex-wrap gap-2">
                        {insurance.coverage.slice(0, 3).map((coverage, index) => (
                          <Badge
                            key={index}
                            variant="secondary"
                            className="border border-green-200 bg-white px-2 py-1 text-xs text-green-700"
                          >
                            {coverage}
                          </Badge>
                        ))}
                        {insurance.coverage.length > 3 && (
                          <Badge
                            variant="secondary"
                            className="border border-green-200 bg-white px-2 py-1 text-xs text-green-700"
                          >
                            +{insurance.coverage.length - 3} mais
                          </Badge>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
