import { format } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { Calendar as CalendarIcon, CheckCircle2, Clock } from 'lucide-react';
import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

import { LoanFinancialSummary } from '@/components/loan/LoanFinancialSummary';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Textarea } from '@/components/ui/textarea';
import { toast } from '@/components/ui/use-toast';
import { cn } from '@/lib/utils';

import type { ItemWithDetails } from '@/types';

interface InsuranceOption {
  id: string;
  name: string;
  description: string;
  coverage: string[];
  price: number;
  deductible: number;
  popular?: boolean;
  recommended?: boolean;
  savings?: string;
}

interface LoanRequestFormProps {
  item: ItemWithDetails;
  onCancel: () => void;
  onSubmit: (data: Record<string, unknown>) => void;
}

export function LoanRequestForm({ item, onCancel, onSubmit }: LoanRequestFormProps) {
  const navigate = useNavigate();
  const location = useLocation();
  const [startDate, setStartDate] = useState<Date | undefined>();
  const [endDate, setEndDate] = useState<Date | undefined>();
  const [reason, setReason] = useState('');
  const [selectedInsurance, setSelectedInsurance] = useState<InsuranceOption | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [startDateOpen, setStartDateOpen] = useState(false);
  const [endDateOpen, setEndDateOpen] = useState(false);

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL',
    }).format(value);
  };

  const calculateTotalDays = () => {
    if (!startDate || !endDate) return 0;
    const diffTime = Math.abs(endDate.getTime() - startDate.getTime());
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24)) + 1; // +1 para incluir o dia inicial
  };

  const calculateTotalPrice = () => {
    const days = calculateTotalDays();
    return days * item.price;
  };

  // Função para lidar com a seleção da data de início
  const handleStartDateSelect = (date: Date | undefined) => {
    setStartDate(date);
    if (date) {
      // Fechar o calendário da data de início
      setStartDateOpen(false);
      // Abrir automaticamente o calendário da data de devolução após um pequeno delay
      setTimeout(() => {
        setEndDateOpen(true);
      }, 200);
    }
  };

  // Função para lidar com a seleção da data de devolução
  const handleEndDateSelect = (date: Date | undefined) => {
    setEndDate(date);
    if (date) {
      // Fechar o calendário da data de devolução
      setEndDateOpen(false);
      // Pequeno scroll suave para mostrar o valor calculado (opcional)
      setTimeout(() => {
        const valueCard = document.querySelector('[data-value-card]');
        if (valueCard) {
          valueCard.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
      }, 300);
    }
  };

  // Verificar se voltou da tela de seguro com uma seleção
  useEffect(() => {
    if (location.state?.selectedInsurance !== undefined) {
      setSelectedInsurance(location.state.selectedInsurance);
    }

    // Restaurar dados da locação se voltou da tela de seguro
    if (location.state?.loanData) {
      const loanData = location.state.loanData;
      if (loanData.startDate) {
        setStartDate(new Date(loanData.startDate));
      }
      if (loanData.endDate) {
        setEndDate(new Date(loanData.endDate));
      }
      if (loanData.reason) {
        setReason(loanData.reason);
      }
    }

    // Limpar o state para evitar problemas de navegação
    if (location.state?.selectedInsurance !== undefined || location.state?.loanData) {
      window.history.replaceState({}, document.title);
    }
  }, [location.state]);

  const handleGoToInsurance = () => {
    if (!startDate || !endDate) {
      return;
    }

    const loanData = {
      item,
      startDate: startDate.toISOString(),
      endDate: endDate.toISOString(),
      totalDays: calculateTotalDays(),
      totalPrice: calculateTotalPrice(),
      reason: reason.trim(),
      currentInsurance: selectedInsurance, // Passar seguro atual se houver
    };

    navigate('/insurance', {
      state: {
        fromLoanFlow: true,
        loanData,
        returnTo: `/items/${item.id}/loan`,
      },
    });
  };

  const handleRemoveInsurance = () => {
    setSelectedInsurance(null);
  };

  const handleSubmit = async () => {
    if (!isFormValid) return;

    setIsSubmitting(true);

    try {
      // Feedback inicial
      toast({
        title: 'Processando solicitação...',
        description: 'Aguarde enquanto confirmamos sua locação.',
      });

      // Simular processamento da solicitação
      await new Promise((resolve) => setTimeout(resolve, 2000));

      // Gerar ID da locação (5 dígitos únicos)
      const generateLoanId = () => {
        const timestamp = Date.now();
        const random = Math.floor(Math.random() * 1000);
        const combined = timestamp + random;
        // Pegar os últimos 5 dígitos para garantir unicidade
        return String(combined).slice(-5);
      };

      const loanId = generateLoanId();

      const loanData = {
        item,
        startDate: startDate?.toISOString(),
        endDate: endDate?.toISOString(),
        totalDays: calculateTotalDays(),
        totalPrice: calculateTotalPrice(),
        insurance: selectedInsurance,
        reason: reason.trim(),
        loanId,
        submittedAt: new Date().toISOString(),
      };

      // Feedback de sucesso
      toast({
        title: 'Locação confirmada!',
        description: 'Sua solicitação foi processada com sucesso.',
      });

      // Pequeno delay para mostrar o feedback
      await new Promise((resolve) => setTimeout(resolve, 500));

      // Navegar diretamente para a tela de sucesso
      navigate('/loan-success', {
        state: loanData,
        replace: true,
      });
    } catch (error) {
      toast({
        title: 'Erro ao processar solicitação',
        description: 'Não foi possível confirmar sua locação. Tente novamente.',
        variant: 'destructive',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  const isFormValid = startDate && endDate && reason.trim().length > 0;

  return (
    <div className="space-y-6">
      {/* Seleção de Datas */}
      <Card>
        <CardHeader className="pb-3">
          <CardTitle className="flex items-center gap-2 text-lg">
            <CalendarIcon className="h-5 w-5 text-primary" />
            Período do Empréstimo
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            {/* Data de Início */}
            <div className="space-y-2">
              <Label className="text-sm font-medium">Data de Retirada</Label>
              <Popover open={startDateOpen} onOpenChange={setStartDateOpen}>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className={cn(
                      'h-11 w-full justify-start text-left font-normal',
                      !startDate && 'text-muted-foreground'
                    )}
                  >
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {startDate
                      ? format(startDate, 'dd/MM/yyyy', { locale: ptBR })
                      : 'Selecionar data'}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={startDate}
                    onSelect={handleStartDateSelect}
                    disabled={(date) => date < new Date()}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>

            {/* Data de Fim */}
            <div className="space-y-2">
              <Label className="text-sm font-medium">Data de Devolução</Label>
              <Popover open={endDateOpen} onOpenChange={setEndDateOpen}>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className={cn(
                      'h-11 w-full justify-start text-left font-normal',
                      !endDate && 'text-muted-foreground'
                    )}
                  >
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {endDate ? format(endDate, 'dd/MM/yyyy', { locale: ptBR }) : 'Selecionar data'}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={endDate}
                    onSelect={handleEndDateSelect}
                    disabled={(date) => date < (startDate || new Date())}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Resumo Financeiro */}
      {startDate && endDate && (
        <div data-value-card className="space-y-3">
          <LoanFinancialSummary
            dailyRate={item.price}
            totalDays={calculateTotalDays()}
            insurance={selectedInsurance}
          />

          {/* Opção de Seguro */}
          {!selectedInsurance ? (
            <div className="flex justify-center">
              <Button
                variant="outline"
                size="sm"
                onClick={handleGoToInsurance}
                className="border-purple-200 text-purple-600 hover:bg-purple-50"
              >
                + Adicionar Seguro (Opcional)
              </Button>
            </div>
          ) : (
            <div className="flex justify-center">
              <Button
                variant="ghost"
                size="sm"
                onClick={handleRemoveInsurance}
                className="text-gray-600 hover:text-gray-800"
              >
                Remover Seguro
              </Button>
            </div>
          )}
        </div>
      )}

      {/* Motivo do Empréstimo */}
      <Card>
        <CardHeader className="pb-3">
          <CardTitle className="text-lg">Finalidade do Empréstimo</CardTitle>
        </CardHeader>
        <CardContent>
          <Textarea
            value={reason}
            onChange={(e) => setReason(e.target.value)}
            placeholder="Descreva brevemente como pretende usar o item..."
            className="min-h-[100px] resize-none"
            required
          />
        </CardContent>
      </Card>

      {/* Botões de Ação */}
      <div className="flex gap-3 pt-2">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          className="h-12 flex-1"
          disabled={isSubmitting}
        >
          Cancelar
        </Button>
        <Button
          onClick={handleSubmit}
          className="h-12 flex-1"
          disabled={!isFormValid || isSubmitting}
        >
          {isSubmitting ? (
            <>
              <Clock className="mr-2 h-4 w-4 animate-spin" />
              Processando...
            </>
          ) : (
            <>
              <CheckCircle2 className="mr-2 h-4 w-4" />
              Confirmar Locação
            </>
          )}
        </Button>
      </div>
    </div>
  );
}
